import { Log, useProxyWatch, useProxyWatchUpdates, useTranslation } from '@fanfanlo';
import { 
    Box, 
    List, 
    ListItem, 
    ListItemAvatar, 
    ListItemText, 
    Avatar, 
    Typography, 
    ListItemSecondaryAction,
    IconButton,
    Chip
} from '@mui/material';
import { IMatrixRoomData, IMatrixRoomInfo, matrixRoom } from '../../../client/room/';
import { format } from 'date-fns';
import { zhCN } from 'date-fns/locale';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import { matrixClient } from '../../../client';
import React from 'react';
import { useRouter } from 'next/router';

const fileLogger = new Log(false, 'im.components.rooms.summary-room-list.SummaryRoomList');
// fileLogger.pause = true;
// fileLogger.childrenPaused = true;

interface RoomListItemProps {
    room: IMatrixRoomInfo;
    onClick: (roomId: string) => void;
    onInviteResponse?: (roomId: string, accept: boolean) => Promise<void>;
}

function RoomListItem({ room, onClick, onInviteResponse }: RoomListItemProps) {
    const logger = fileLogger.sub(false, 'RoomListItem_ui');
    logger.pause = true;
    logger.log('room=', room);
    const [name] = useProxyWatch(room, 'name', room.name);
    const [avatarUrl] = useProxyWatch(room, 'avatarUrl', room.avatarUrl);
    const [isProcessing, setIsProcessing] = React.useState(false);
    const { t } = useTranslation('homepage/im/components/rooms/summary-room-list/content');

    const handleInviteResponse = async (e: React.MouseEvent, accept: boolean) => {
        e.stopPropagation();
        if (onInviteResponse && !isProcessing) {
            setIsProcessing(true);
            try {
                await onInviteResponse(room.roomId, accept);
            } finally {
                setIsProcessing(false);
            }
        }
    };

    const getRoomName = () => {
        if (!room.name) return room.roomId;
        return room.name;
    };

    // 用 useProxyWatch 监听 lastMessages
    const [lastMsg] = useProxyWatch(matrixRoom.roomData.lastMessages, [room.roomId], matrixRoom.roomData.lastMessages[room.roomId]);
    logger.log('lastMsg=', lastMsg);
    logger.log('lastMessages=', matrixRoom.roomData.lastMessages);
    let lastMsgSummary = '';
    if (lastMsg) {
        const { msgtype, body } = lastMsg.content || {};
        if (msgtype === 'm.text') lastMsgSummary = body;
        else if (msgtype === 'm.image') lastMsgSummary = '[图片]';
        else if (msgtype === 'm.file') lastMsgSummary = `[文件] ${body}`;
        else if (msgtype === 'm.video') lastMsgSummary = '[视频]';
        else if (msgtype === 'm.audio') lastMsgSummary = '[音频]';
        else if (msgtype === 'm.location') lastMsgSummary = '[位置]';
        else lastMsgSummary = '[消息]';
    }

    // @ts-ignore: membership 字段在 IMatrixRoomInfo 上
    const isInvite = room.membership === 'invite';

    return (
        <ListItem 
            onClick={() => onClick(room.roomId)}
            sx={{
                bgcolor: 'background.paper',
                opacity: isProcessing ? 0.7 : 1,
                pointerEvents: isProcessing ? 'none' : 'auto'
            }}
            secondaryAction={
                isInvite ? (
                    <Box sx={{ display: 'flex', gap: 1 }}>
                        <IconButton
                            size="small"
                            color="primary"
                            onClick={(e) => handleInviteResponse(e, true)}
                            title={t('SummaryRoomList.acceptInvite')}
                            disabled={isProcessing}
                        >
                            <CheckIcon />
                        </IconButton>
                        <IconButton
                            size="small"
                            color="error"
                            onClick={(e) => handleInviteResponse(e, false)}
                            title={t('SummaryRoomList.rejectInvite')}
                            disabled={isProcessing}
                        >
                            <CloseIcon />
                        </IconButton>
                    </Box>
                ) : (
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        {(room.unreadCount ?? 0) > 0 && (
                            <Chip 
                                label={room.unreadCount?.toString() ?? '0'} 
                                size="small" 
                                sx={{ 
                                    bgcolor: 'primary.main', 
                                    color: 'primary.contrastText',
                                    px: 1
                                }}
                            />
                        )}
                    </Box>
                )
            }
        >
            <ListItemAvatar>
                <Avatar src={avatarUrl}>
                    {name.charAt(0)}
                </Avatar>
            </ListItemAvatar>
            <ListItemText
                primary={name}
                secondary={lastMsgSummary}
            />
        </ListItem>
    );
}


export function SummaryRoomList() {
  // i18n文件路径规则
  // i18n文件路径，这个文件将被工具自动生成另一个json文件放置到public和一个i18n-raw的目录，例如当前这个就在这里
  // C:\work\android-droid\html\website-2024-12\apps\homepage\i18n-raw\origin\im\components\rooms\summary-room-list\content.json
  // apps\homepage\src\im\components\rooms\summary-room-list\SummaryRoomList.i18n.json
  // 这是一个monorepo架构方式的项目，
  // homepage是项目名，和package.json对应，
  // /im/components/rooms/summary-room-list是当前文件的目录
  // content是一个后缀，自动生成的i18n文件会自定命名为content.json
  // 使用时的规则就是 `${package.name}/${fileFolder}/content`
  // 这个文件会映射到${package.name}/src/types/i18n-resources.ts文件里
  // 例如当前这个对应在   homepage__im__components__rooms__summary___room___list 这个type就是它的json文件的类型。
  // useTranslation 用的是 @fanfanlo库里导入的，而非react-i18next。
  const { t } = useTranslation('homepage/im/components/rooms/summary-room-list/content');

  // 用useProxyWatchUpdates来监听房间列表变化
  const [rooms] = useProxyWatchUpdates(matrixRoom.roomData, 'joinedRooms', matrixRoom.roomData.joinedRooms)

  const router = useRouter();
  // 处理邀请响应
  const handleInviteResponse = async (roomId: string, accept: boolean) => {
    try {
      if (accept) {
        // 接受邀请
        await matrixClient.client.joinRoom(roomId);
      } else {
        // 拒绝邀请
        await matrixClient.client.leave(roomId);
      }
    } catch (error) {
      console.error('处理邀请时出错:', error);
      // 这里可以添加错误提示
    }
  };
  const createHandleRoomClick = (roomId: string) => {
    return () => {
      handleRoomClick(roomId)
    };
  };
  // 处理房间点击
  const handleRoomClick = (roomId: string) => {
    // 这里可以添加导航到聊天界面的逻辑
    console.log('进入房间:', roomId);
    router.push(`/im/room?id=${encodeURIComponent(roomId)}`);
  };

  if (rooms.length === 0) {
    return (
      <Box sx={{ p: 2, textAlign: 'center', color: 'text.secondary' }}>
        {/* SummaryRoomList是i18n文件名，也是要用的key前缀。 */}
        {t('SummaryRoomList.noRooms')}
      </Box>
    );
  }

  return (
    <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
      {rooms.map((room) => (
        <RoomListItem 
          key={room.roomId} 
          room={room} 
          onClick={createHandleRoomClick(room.roomId)}
          onInviteResponse={handleInviteResponse}
        />
      ))}
    </List>
  );
}
